*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: whitesmoke;
}

svg {
  display: inline;
  vertical-align: middle;
  height: 1em;
  fill: currentColor;
}

button {
  color: inherit;
}
